<template>
<v-container grid-list-xl fluid>
    <v-layout row wrap>

        <v-flex sm12>
            <h3>Usuário</h3>
        </v-flex>

        <v-flex sm12>
            <v-card color="blue-grey darken-2" class="white--text">
                <v-card-title primary-title>
                    <div>
                        <div class="headline">{{client.name}}</div>
                        <span>{{client.email}}</span>
                        <br>
                         <span>{{client.funcao}}</span>
                    </div>
                </v-card-title>
                <!-- <v-card-actions>
                    <v-btn flat dark>Listen now</v-btn>
                </v-card-actions> -->
            </v-card>
        </v-flex>

         <v-flex sm12>
            <h3>Documentos</h3>
        </v-flex>

        <v-flex xs12 sm12 md12>
            <v-card>
                <v-toolbar color="">
                    <v-toolbar-title>
                        <v-icon>search</v-icon>
                    </v-toolbar-title>
                    <v-text-field :loading="loading" v-model="search" class="mx-3" flat hide-no-data hide-details label="Buscar" solo-inverted></v-text-field>
                    <!-- <cadastrar-diretor  @reload='listarDiretores'></cadastrar-diretor> -->
                </v-toolbar>

                <v-list subheader>
                    <v-list-tile v-for="item in filteredList" :key="item._id" @click="getDownalod(item._id)">
                        <v-list-tile-content>
                            <v-list-tile-title v-html="item.title"></v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list>
            </v-card>
        </v-flex>

    </v-layout>
</v-container>
</template>

<script>
import services from "../services/userService";

export default {
    data() {
        return {
            user: {},
            client: {},
            search: '',
            filers: [],
            id: ''
        }
    },
    methods: {
        listarArquivos(id) {
            services.getFiles(id).then(r => {
                if (r.status == 200) {
                    this.filers = r.data;
                }
            });
        },
        getDownalod(data) {
            services.fileDownload(data).then(r => {
                if (r.status == 200) {
                    var url = 'http://191.252.200.167:3000/file/download/' + data;
                    window.open(url)
                }
            });
        },
        getUser(id) {
            console.log(id);
            
            services.getUser(id).then(r => {
                console.log(r);
                
                if (r.status == 200) {
                  this.client = r.data;
                }
            });
        }
    },
    mounted() {
        this.user = this.user = JSON.parse(this.$cookie.get("user"));
        this.id = this.$route.params.id;
        this.listarArquivos(this.id);
        this.getUser(this.id);
    },
    computed: {
        filteredList() {
            return this.filers.filter(post => {
                return post.name.toLowerCase().includes(this.search.toLowerCase())
            })
        }
    }

}
</script>
